@import '../../common/styles/mixin';
@import '../../common//styles/variables';

.privacy-container {
    .banner_section {
        .privacy-banner {
            width: 100%;
            height: 100vh;
            background: url('../../assets/images/others/privacy_policy.jpg') no-repeat;
            background-color: rgba(66,66,41,0.6);
            background-size: cover;
            background-position: center center;
            background-attachment: fixed;
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;

            @include font('Roboto', 50px, 600, $white);
        }
    }

    .privacy-body{
        .privacy-form {
            // display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;

            h2 {
                padding: 40px 0;
                @include font('Roboto', 40px, 600);
            }

            h3 {
                padding: 40px 0;
                @include font('Roboto', 30px, 400);
            }

            ul {
                padding: 0px 10px 10px 40px;
                list-style: circle;
            }

            p {
                padding: 0px 0 20px;
                line-height: 25px;
                @include font('Roboto', 18px, 400, $description);
            }

        }
    }

    .MuiTextField-root {
        width: 100%;
    }

}

